.detail {
  margin-top: 100px;
  width: 580px;
  background-color: white;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.detail .detail__title {
  font-weight: 500;
  font-size: 20px;
  margin-bottom: 15px;
  color: #2c2c2c;
}
